@use "../abstracts/color-vars" as col;
@use "../abstracts/mixins" as mix;
@use"../core/fonts-vars" as ff;

.nav-bk-color {
	background-color: rgba(255, 255, 255, 1) !important;
	//transform: translateY(0);
	transition: background-color 250ms ease-in-out;
	box-shadow: 1px 1px 1px 1px rgba(228, 110, 84, 0.2);
}

.nav-container {
	grid-area: nav;
	width: 100%;
	height: max-content;
	position: sticky;
	top: 0;
	z-index: 4;
	
	//	background-color: palegreen;
	@include mix.media(768px) {
		height: 94.2px;
		}

	@include mix.media(375px){
		height: 100px;		
	}
}

 .logo {
	padding: 15px;
	position: absolute;
	top: 0;
	z-index: 50;
}

.font2{
	font-family: ff.$ffm-logo ;
	position: relative;

	@include mix.media(768px) {
		font-size: 12px;
		top: 32px;
		left: 5px;
	}
}

.font3{
	font-family: ff.$ffm-logo;
	position: relative;

	@include mix.media(768px) {
		font-size: 14px ;
		top: 38px;
		left: 5px;
	}
}

.nav-wrapper {
	@include mix.ul-style(none, 5px);
	width: 100%;
	//	background-color: rgb(255, 255, 255);
	@include mix.media(768px) {
		transform: translateY(-5000px);
		transition: transform 100ms ease;
		//	background-color: rgb(15, 0, 128);
	}
}
.nav-wrapper :nth-child(3) {
	margin-left: 50px;
	@include mix.media(768px) {
		margin-left: 0;
	}
}

.nav-item {
	//color: black;
	//display: inline-block;
	padding: 15px;
}

.nav-link {
	@include mix.link-remove-default(none, 4px);

	@include mix.links(
		col.$primary-color-nav,
		col.$primary-color-a-hover,
		col.$primary-color-a-active
	);
}
