@use "../abstracts/mixins" as mix;


/* .nav-container {
}
 */
.nav-wrapper {
	display: flex;
	//flex-direction: column;
	justify-content: flex-end;
}

.nav-item {
	display: flex;
	flex-direction: column;
	align-items: center;
}

// .header {
// 	@include mix.media(900px) {
// 		margin: 0 auto;
// 	}
// }

.header-background-trans {
	display: flex;
	flex-direction: column;
	align-items: center;
	@include mix.media(375px){
		align-items: center;
	}
	
}

.header-text-wrapper{
	display: flex;
	flex-direction: column;
	align-items: center;
	//flex: 1 1 max-content;

	@include mix.media(375px){
		width: 100%;
		
}

}

.hero {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;

	@include mix.media(900px) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}
}

.info-container{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.info-title-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.info-text {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.about-section {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.about-title-wrapper {
	display: flex;
	flex-wrap: wrap;
}

.services-section {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.service-title-wrapper {
	display: flex;
	flex-direction: column;
}

/*  footer{
	display: flex;
	flex-direction: column;
}
 */
.form-wrapper {
	display: flex;
	justify-content: center;

	@include mix.media(600px){
		justify-content: left;
	}


}

.form-item{
	display: flex;
	flex-direction: column;
	//align-items: center;
}
