@use "../abstracts/mixins" as mix;


body {
	width: 100%;
	background-image: url("../../images/triangles.png");
	background-repeat: repeat;
	background-color: #14226b;
	//	position: relative;
}

.main-page-container {
	width: 80%;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-areas:
		"nav nav nav nav"
		"header header header header"
		"main main main main"
		"foot foot foot foot ";
	background-color: #cbe6f8;

	@include mix.media(900px) {
		grid-template-columns: repeat(2, 1fr);
		grid-template-areas:
			"nav nav"
			"header header"
			"main main"
			"foot foot";
		width: 100%;
	}

	@include mix.media(600px) {
		grid-template-columns: 1fr;
		grid-template-areas:
			"nav"
			"header"
			"main"
			"foot";	
			//width: 95%;	
	}
}


.header {	
	display: grid;
	grid-template-columns: 1fr;
}



.hero-section {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	@include mix.media(900px) {
		grid-template-columns: 1fr;
	}
/* 	@include mix.media(480px) {
		grid-template-columns: 1fr;
	} */ 
}

.info-section {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 5px;

	@include mix.media(900px) {
		grid-template-columns: 1fr 1fr;
	}

	@include mix.media(768px) {
		grid-template-columns: 1fr;
	}
}

.about-section {
	display: grid;
	grid-template-columns: repeat(2, 1fr);

	@include mix.media(900px) {
		grid-template-columns: 1fr;
	}
 
/* 	 @include mix.media(414px) {
	 	grid-template-columns: 1fr;
	} */ 
}

.services-section {
	display: grid;
	grid-template-columns: repeat(2, 1fr);

	@include mix.media(900px) {
		grid-template-columns: 1fr;
	}

 	/* @include mix.media(414px) {
		grid-template-columns: 1fr;
	} */ 
}
.footer-wrapper {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	@include mix.media(600px){
		grid-template-columns: 1fr;
	}
}
