@use "../abstracts/mixins" as mix;


/* svg {
	width: 32px;
	height: 32px;
} */


.main-logo{
width: 10%;
height: auto;
display: inline-block;
}


.icon-img {
	width: 50px;
	height: 50px;
	margin-top: 7.5px;
	margin-bottom: 7.5px;
	background-color: white;
	padding: 7px;
	border: 1px solid black;
	border-radius: 0 10px 0 10px;
}

.nav-icons {
	width: 24px;
	height: 24px;
	padding-bottom: 3px;
}

.art {
	width: 100%;
	height: auto;
	padding: 15px;
}

.svg-img {
	width: 100%;
	height: 300px;
}

#hamburger {
	width: 175px;
	height: 175px;
}

.hero-img {
	width: 80%;
	height: auto;
	margin-top: 75px;
	margin-left: 25px;

	@include mix.media(900px) {
		width: 50%;
		height: auto;

		margin: 0 auto;
	}
}

.about-img {
	width: 65%;
	grid-column: 2;
	grid-row: 3;
	height: auto;
	margin: 20px auto;
	border-radius: 5px;

	box-shadow: 3px 3px 8px #222121;

@include mix.media(900px){
	width: 60%;
	margin: 0 auto;
	grid-column: 1;
	grid-row: 2;
}

}

.service-img {
	grid-column: 1;
	grid-row: 3;
	width: 80%;
	height: auto;
margin: 0 auto;
	//padding: 25px;
	border-radius: 5px;
	box-shadow: 3px 3px 8px #222121;

	@include mix.media(900px){
		width: 60%;
		grid-column: 1;
	grid-row: 4;
		//margin: 0 auto;
	}
}
