@use"../abstracts/mixins" as mix;


.services-section {
width: 100%;
	padding: 25px;
	background-color: rgba(237, 92, 179, .4);
}

.service-title-wrapper {
	grid-column: 1 / -1;
	width: 100%;
	height: max-content;
	text-align: center;
	padding: 20px;

}


.service-title{
	width: 100%;
	padding: 10px;
}

.service-title-text{
	width: 100%;
	padding: 10px;
	@include mix.media(414px){
		padding: 4px;
	}
}



.service-title-content {
	grid-column: 1 span 1;
	width: 100%;
	padding: 15px;
}
