		background-image: url("data:image/svg+xml,%3Csvg width='325' height='-175' viewBox=' -35 0 150 150' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Polygon 7'%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='white'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='black'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' stroke='white'/%3E%3C/g%3E%3C/svg%3E%0A");
		
