.info-title-wrapper {
	width: 100%;
	height: max-content;
	//grid-column: 1 / -1;
//	padding: 50px;
	color: #eaba32;
	
}

.info-title {
	@include mix.media(768px) {
		width: 100%;
		text-align: center;
	}
}

.info-title-wrapper > span {
	background-color: #eaba32;
	margin: 60px;
  
	width: 175px;
	height: 2px;
}

.info-title-text {
 margin-bottom: 65px;
	
	width: 95%;
	padding: 35px;
	border: 1px solid rgb(245, 242, 242);
	border-radius: 5px;
	//justify-items: left;
	//background-color: rgb(20, 2, 2);
	color: gold;
	box-shadow: 1px 1px 1px 1px #b8b4b4;

}

.faded {
	opacity: 0;
	transform: translate3d(0, 5rem, 0);
	transition: transform 1s cubic-bezier(0.64, 0.04, 0.26, 0.87), o;
}

.show {
	opacity: 1;
	transform: translate3d(0, 0, 0);
}

.info-text {
	width: 100%;
	height: 100%;
	grid-column: 1 span 1;
	border: 1px solid black;
	padding: 10px;
	background-color: #b3c9ef;
	color: #090113;

	@include mix.media(900px) {
		width: 100%;
		padding: 25px;
	}
}

.info-text:last-child {
	@include mix.media(900px) {
		grid-column: 1 / -1;
	}
}

.info-title {
	padding: 25px;
	
	@include mix.media(900px) {
		padding: 25px;
	}
}
