@use "../abstracts/mixins" as mix;

.hero-section {
	width: 100%;
	height: max-content;
}

.hero {
	width: 100%;
	height: max-content;

	@include mix.media(900px) {
		width: 100%;
	}
}

.hero :nth-child(1) {
	margin-top: 25px;
}

.hero-text {
	margin-top: 25px;

	@include mix.media(900px) {
		padding: 20px;
	}
}

.hero-ul-wrapper > li {
	//margin: 5px;
	padding: 10px;
}

