@use "../abstracts/color-vars" as col;
@use "../components/buttons" as btn;
@use "../core/fonts-vars" as ff;
@use"../abstracts/mixins" as mix;

footer {
	grid-area: foot;
	width: 100%;
	height: max-content;
	background-color: #f4f7f2;
	color: rgb(6, 6, 6);
}

.font {
	font-family: ff.$ffm-logo;
	font-size: 24px;
}

.font1 {
	font-family: ff.$ffm-logo;
	font-size: 18px;
}

.footer-wrapper {
	//grid-column: 1 / -1;
	background-color: black;
	color: white;
}

.form-wrapper {
	grid-column: 1 / -1;
	background-color: blue;
	padding: 20px;
}

.footer-foot {
	grid-column: 1 / -1;
	grid-row: 3;
	padding: 20px;
}

.form-item {
	width: 100%;
}

.f-label {
	width: 50%;
	padding: 10px;
	 background-color: black;
	 color: white;
	

	@include mix.media(600px) {
		width: 25%;
		padding: 5px;
		margin-top: 5px;
		margin-bottom: 5px;
		text-align: center;
		

	}
}
.f-input {
	padding: 10px;
	outline: none;

	@include mix.media(600px) {
		width: 75%;
	}
}

.form-error {
	padding: 3px;
}

.btn-wrapper{
	padding: 15px;

}
