@use "../abstracts/mixins" as mix;
// @use "../components/" as com;
// @use "../core/" as c;
// @use "../layout/" as lay;
// @use "pages" as pa;

header {
	grid-area: header;
	width: 100%;
	height: max-content;
	//	background-color: pink;
	background-image: url("/images/art-img.png");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	//align-items: center;
}

.header-background-trans {
	grid-column: 1/ -1;
	width: 95%;
	height: max-content;
	margin: 30px;
	padding: 10px;
	background-color: #ffffff;
	border: 1px solid black;
	opacity: 0.9;
	//text-align: center;

	@include mix.media(900px) {
		//	margin: 5px;
		padding: 5px;
	}

	@include mix.media(375px) {
		width: 75%;
		padding: 5px;
		margin: 0 !important;
	}
}

.header-text-wrapper {
	width: 100%;
}

.header-text-wrapper :nth-child(2) {
	margin-top: 5px;
	margin-bottom: 5px;
}

.header-title {
	padding-top: 10px;
}
